"use client";

import { useState, useEffect, useRef, useMemo } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import PackageCard from "./PackageCard";
import "../styles/PackageList.css";

function PackageList({ filters = {}, packageType = "all" }) {
  const location = useLocation();
  const navigate = useNavigate();
  const packagesPerPage = 6;
  const packageListRef = useRef(null);
  const [packagesData, setPackagesData] = useState([]);
  const [loading, setLoading] = useState(true);

  // Fetch packages data from API
  useEffect(() => {
    const fetchPackagesData = async () => {
      try {
        const response = await fetch("/api/packages");
        const data = await response.json();

        if (data.success) {
          setPackagesData(data.packages || []);
        } else {
          console.error("Failed to fetch packages data:", data.message);
        }
      } catch (error) {
        console.error("Error fetching packages data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchPackagesData();
  }, []);

  // Get current page from URL
  const getPageFromUrl = () => {
    const searchParams = new URLSearchParams(location.search);
    const page = searchParams.get("page");
    return page ? Number.parseInt(page, 10) : 1;
  };

  // Set current page based on URL
  const [currentPage, setCurrentPage] = useState(getPageFromUrl());

  // Use useMemo to optimize filtering logic
  const filteredPackages = useMemo(() => {
    let result = [...packagesData];

    // Filter by package type (domestic or international)
    if (packageType === "domestic") {
      result = result.filter(
        (pkg) => pkg.location && pkg.location.toLowerCase().includes("india")
      );
    } else if (packageType === "international") {
      result = result.filter(
        (pkg) => !pkg.location || !pkg.location.toLowerCase().includes("india")
      );
    }

    // Apply filters
    if (filters.destination) {
      const searchTerm = filters.destination.toLowerCase();

      // Define common words to filter out
      const commonWords = [
        "india",
        "the",
        "and",
        "&",
        ",",
        "-",
        "of",
        "in",
        "at",
        "to",
        "for",
        "with",
        "by",
        "a",
        "an",
        "escape",
        "retreat",
        "tour",
        "adventure",
        "getaway",
        "vacation",
        "holiday",
        "trip",
        "experience",
        "expedition",
        "journey",
        "splendor",
        "bliss",
        "explorer",
        "package",
        "packages",
        "himalayan",
      ];

      // Extract meaningful words from the search query
      const searchWords = searchTerm
        .split(/[\s,&-]+/)
        .filter((word) => word.length > 2 && !commonWords.includes(word));

      // If no meaningful words found, use the original search term
      if (searchWords.length === 0) {
        result = result.filter(
          (pkg) =>
            pkg.name.toLowerCase().includes(searchTerm) ||
            pkg.location.toLowerCase().includes(searchTerm)
        );
      } else {
        // Filter packages that match any of the meaningful words
        result = result.filter((pkg) => {
          const packageName = pkg.name.toLowerCase();
          const packageLocation = pkg.location.toLowerCase();

          // Check if any search word is found in the package name or location
          return searchWords.some(
            (word) =>
              packageName.includes(word) || packageLocation.includes(word)
          );
        });
      }
    }

    if (filters.duration) {
      const [min, max] = filters.duration.split("-").map(Number);
      result = result.filter((pkg) => {
        if (max) {
          return pkg.duration >= min && pkg.duration <= max;
        } else {
          // For "15+" case
          return pkg.duration >= min;
        }
      });
    }

    if (filters.budget) {
      const [min, max] = filters.budget.split("-").map(Number);
      result = result.filter((pkg) => {
        if (max) {
          return pkg.price >= min && pkg.price <= max;
        } else {
          // For "3000+" case
          return pkg.price >= min;
        }
      });
    }

    // Apply sorting
    if (filters.sortBy) {
      switch (filters.sortBy) {
        case "price-low":
          result.sort((a, b) => a.price - b.price);
          break;
        case "price-high":
          result.sort((a, b) => b.price - a.price);
          break;
        case "duration-low":
          result.sort((a, b) => a.duration - b.duration);
          break;
        case "duration-high":
          result.sort((a, b) => b.duration - a.duration);
          break;
        default:
          break;
      }
    }

    return result;
  }, [filters, packageType, packagesData]);

  // Calculate total pages
  const totalPages = Math.ceil(filteredPackages.length / packagesPerPage);

  // Validate current page and redirect to 404 if invalid
  useEffect(() => {
    const pageFromUrl = getPageFromUrl();

    // Check if page number is invalid
    if (pageFromUrl < 1 || (totalPages > 0 && pageFromUrl > totalPages)) {
      // Redirect to 404 page for invalid page numbers
      navigate("/404", { replace: true });
      return;
    }

    setCurrentPage(pageFromUrl);
  }, [location.search, totalPages, navigate]);

  // Additional validation for manual URL changes
  useEffect(() => {
    if (totalPages > 0 && currentPage > totalPages) {
      navigate("/404", { replace: true });
    }
  }, [currentPage, totalPages, navigate]);

  // Get current packages
  const indexOfLastPackage = currentPage * packagesPerPage;
  const indexOfFirstPackage = indexOfLastPackage - packagesPerPage;
  const currentPackages = filteredPackages.slice(
    indexOfFirstPackage,
    indexOfLastPackage
  );

  // Change page
  const paginate = (pageNumber) => {
    // Validate page number before navigation
    if (pageNumber < 1 || pageNumber > totalPages) {
      navigate("/404", { replace: true });
      return;
    }

    // Create new URL with updated page parameter
    const searchParams = new URLSearchParams(location.search);
    searchParams.set("page", pageNumber.toString());

    // Navigate to the new URL
    navigate(`${location.pathname}?${searchParams.toString()}${location.hash}`);

    // Scroll to top of package list container
    if (packageListRef.current) {
      packageListRef.current.scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    }
  };

  // Generate pagination display with exactly 2 visible page numbers
  const renderPaginationButtons = () => {
    const items = [];

    // Always show only 2 pages at a time
    if (totalPages <= 2) {
      // If only 1 or 2 pages, show all of them
      for (let i = 1; i <= totalPages; i++) {
        items.push(
          <button
            key={`page-${i}`}
            onClick={() => paginate(i)}
            className={`package-page-number ${
              currentPage === i ? "package-page-active" : ""
            }`}
            aria-label={`Page ${i}`}
            aria-current={currentPage === i ? "page" : undefined}
          >
            {i}
          </button>
        );
      }
    } else {
      // If more than 2 pages, show only 2 at a time
      if (currentPage === 1) {
        // On first page, show pages 1 and 2
        items.push(
          <button
            key="page-1"
            onClick={() => paginate(1)}
            className="package-page-number package-page-active"
            aria-label="Page 1"
            aria-current="page"
          >
            1
          </button>
        );

        items.push(
          <button
            key="page-2"
            onClick={() => paginate(2)}
            className="package-page-number"
            aria-label="Page 2"
          >
            2
          </button>
        );

        // Add ellipsis if there are more than 2 pages
        if (totalPages > 2) {
          items.push(
            <span
              key="ellipsis"
              className="package-page-ellipsis"
              aria-hidden="true"
            >
              ...
            </span>
          );
        }
      } else if (currentPage === totalPages) {
        // On last page, show second-to-last and last page

        // Add ellipsis if there are more than 2 pages from the beginning
        if (currentPage > 2) {
          items.push(
            <span
              key="ellipsis"
              className="package-page-ellipsis"
              aria-hidden="true"
            >
              ...
            </span>
          );
        }

        items.push(
          <button
            key={`page-${totalPages - 1}`}
            onClick={() => paginate(totalPages - 1)}
            className="package-page-number"
            aria-label={`Page ${totalPages - 1}`}
          >
            {totalPages - 1}
          </button>
        );

        items.push(
          <button
            key={`page-${totalPages}`}
            onClick={() => paginate(totalPages)}
            className="package-page-number package-page-active"
            aria-label={`Page ${totalPages}`}
            aria-current="page"
          >
            {totalPages}
          </button>
        );
      } else {
        // In the middle pages

        // Add first page and ellipsis if not on page 2
        if (currentPage > 1) {
          // If we're on page 2, just show page 1
          if (currentPage === 2) {
            items.push(
              <button
                key="page-1"
                onClick={() => paginate(1)}
                className="package-page-number"
                aria-label="Page 1"
              >
                1
              </button>
            );
          } else {
            // If we're on page 3 or higher, show page 1 and ellipsis
            items.push(
              <button
                key="page-1"
                onClick={() => paginate(1)}
                className="package-page-number"
                aria-label="Page 1"
              >
                1
              </button>
            );

            items.push(
              <span
                key="ellipsis-start"
                className="package-page-ellipsis"
                aria-hidden="true"
              >
                ...
              </span>
            );
          }
        }

        // Add current page
        items.push(
          <button
            key={`page-${currentPage}`}
            onClick={() => paginate(currentPage)}
            className="package-page-number package-page-active"
            aria-label={`Page ${currentPage}`}
            aria-current="page"
          >
            {currentPage}
          </button>
        );

        // If not on second-to-last page, show next page and possibly ellipsis
        if (currentPage < totalPages - 1) {
          items.push(
            <button
              key={`page-${currentPage + 1}`}
              onClick={() => paginate(currentPage + 1)}
              className="package-page-number"
              aria-label={`Page ${currentPage + 1}`}
            >
              {currentPage + 1}
            </button>
          );

          // Add ellipsis and last page if there are more pages after next page
          if (currentPage + 1 < totalPages) {
            items.push(
              <span
                key="ellipsis-end"
                className="package-page-ellipsis"
                aria-hidden="true"
              >
                ...
              </span>
            );

            // Add last page number if we're not close to it
            if (currentPage < totalPages - 2) {
              items.push(
                <button
                  key={`page-${totalPages}`}
                  onClick={() => paginate(totalPages)}
                  className="package-page-number"
                  aria-label={`Page ${totalPages}`}
                >
                  {totalPages}
                </button>
              );
            }
          }
        } else {
          // On second-to-last page, show last page
          items.push(
            <button
              key={`page-${totalPages}`}
              onClick={() => paginate(totalPages)}
              className="package-page-number"
              aria-label={`Page ${totalPages}`}
            >
              {totalPages}
            </button>
          );
        }
      }
    }

    return items;
  };

  if (loading) {
    return (
      <div className="package-list-container" ref={packageListRef}>
        <div className="package-loading">
          <p>Loading packages...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="package-list-container" ref={packageListRef}>
      {filteredPackages.length === 0 ? (
        <div className="package-no-results">
          <i className="fas fa-search fa-3x"></i>
          <h3>No packages found matching your criteria</h3>
          <p>
            Try adjusting your search filters or browse our other destinations
          </p>
        </div>
      ) : (
        <>
          <div className="package-results-summary">
            <p>
              Found <span>{filteredPackages.length}</span> packages matching
              your criteria
              <span className="package-page-indicator">
                (Page {currentPage} of {totalPages})
              </span>
            </p>
          </div>

          <div className="package-grid">
            {currentPackages.map((pkg) => (
              <div key={pkg.id} className="package-card-wrapper">
                <PackageCard key={pkg.id} package={pkg} />
              </div>
            ))}
          </div>

          {totalPages > 1 && (
            <div className="package-pagination">
              <button
                onClick={() => paginate(currentPage - 1)}
                disabled={currentPage === 1}
                className="package-pagination-button"
                aria-label="Previous page"
              >
                <i className="fas fa-chevron-left"></i> Previous
              </button>

              <div className="package-page-numbers">
                {renderPaginationButtons()}
              </div>

              <button
                onClick={() => paginate(currentPage + 1)}
                disabled={currentPage === totalPages}
                className="package-pagination-button"
                aria-label="Next page"
              >
                Next <i className="fas fa-chevron-right"></i>
              </button>
            </div>
          )}
        </>
      )}
    </div>
  );
}

export default PackageList;
